.side-drawer {
  height: 100%;
  background: #485563;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.8);
  position: fixed;
  border: 1 solid rgb(31, 121, 173);
  border-radius: 1.5px; 
  top: 0;
  left: 0;
  width: 70%;
  max-width: 250px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.side-drawer li {
  margin: 0.5rem 0;
}

.side-drawer a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: rgb(31, 121, 173);
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}